import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import { Row, Card, Button, Table, Col, Form, Input, Select, Spin, message } from 'antd';
import BtnAddNew from 'components/common/button/BtnAddNew';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import DatePickerString from 'components/common/input/DatePickerString';
import {
  getBonusType,
  getBonusTypeChild,
  addItemClassReward,
  getAddRewardById,
  getListClass,
} from 'services/ClassReward';

const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    className: '',
    regNum: '',
    decDay: '',
    rewardLevel: '',
    form: '',
    typeOfReward: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    teacherId: false,
    initialValue: {},
    dataAddReward: {},
    isPending: false,
    bonusType: [],
    bonusTypeChild: {},
    bonusTypeChildNew: [],
    listOptionClass: [],
  };

  componentDidMount = () => {
    const { match } = this.props;
    getBonusType()
      .then(res => {
        this.setState({
          bonusType: res.data.items,
        });
      })
      .catch(error => {
        console.log(error);
      });
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getAddRewardById(match.params.id);
    }
  };

  getListClassId = () => {
    this.props.form.validateFields(['yearEduId', 'semesterId'], (err, values) => {
      if (err) {
        return;
      }
      getListClass(values).then(res => {
        this.setState({
          listOptionClass: res.data.items,
        });
      });
    });
  };

  getAddRewardById = async id => {
    const { form } = this.props;
    const dataMap = await getAddRewardById(id);
    console.log('dataMap', dataMap);
    const initialValue = handleInitialValue(dataMap.data);
    dataMap.data.students.forEach(student => {
      this.chooseBonusType(student.parentBonusTypeId);
    });
    form.setFieldsValue(initialValue);
    this.setState({ initialValue, dataAddReward: dataMap.data.students, dataGetId: dataMap.data });
    console.log('initialValue', initialValue);
  };

  handleSubmit = e => {
    e.preventDefault();
    const { dataAddReward, teacherId, dataGetId } = this.state;
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (!Object.values(dataAddReward).length) {
          message.warning('Phải chọn lớp !');
          return;
        }
        const data = [];
        console.log('values', values);
        if (teacherId) {
          const student = [];
          // eslint-disable-next-line array-callback-return
          dataAddReward.map(item => {
            const dataItem = {
              studentId: item.id,
              classId: item.classId,
              bonusTypeId: item.bonusTypeId,
            };
            student.push(dataItem);
          });
          const dataAdd = {
            approvalDate: values.approvalDate,
            approvalNumber: values.approvalNumber,
            description: values.description,
            id: dataGetId.id,
            semesterId: values.semesterId,
            yearEduId: values.yearEduId,
            students: student,
          };

          addItemClassReward(dataAdd)
            .then(() => {
              // console.log('res', res);
              // message.success('');
              this.props.history.goBack();
            })
            .catch(error => console.log('error', error));
        } else {
          Object.values(dataAddReward).forEach(item => {
            const { classId, bonusTypeId } = item;
            const dataAddnew = {
              classId,
              bonusTypeId,
            };

            data.push(dataAddnew);
          });

          const addRewardNew = {
            ...values,
            classes: data,
          };
          addItemClassReward(addRewardNew)
            .then(() => {
              this.props.history.goBack();
            })
            .catch(error => {
              console.log('error', error);
            });
        }
      }
    });
  };

  addDataAddReward = () => {
    const { dataAddReward } = this.state;
    const randomCode = this.makeid(8);

    dataAddReward[randomCode] = {
      randomCode,
    };
    this.getListClassId();
    this.setState({
      dataAddReward,
    });
  };

  changeOptionClass = (record, value, option) => {
    const { dataAddReward, listOptionClass } = this.state;
    const classOption = listOptionClass.filter(item => item.classId === value);
    console.log('value', value);
    console.log('option', option);
    console.log('classOption', classOption);
    const { randomCode } = record;
    dataAddReward[randomCode] = {
      randomCode,
      ...classOption[0],
    };
    console.log('dataAddReward', dataAddReward);
    this.setState({
      dataAddReward,
    });
  };

  deleteData = record => {
    const { dataAddReward } = this.state;
    delete dataAddReward[record.randomCode];
    this.setState({
      dataAddReward,
    });
  };

  // changeInputCode = (e, record, number) => {
  //   const { dataAddReward } = this.state;
  //   if (!e.target.value) {
  //     return;
  //   }
  //   this.props.form.validateFields(['yearEduId', 'semesterId'], (err, values) => {
  //     if (err) {
  //       return;
  //     }
  //     this.setState({
  //       isPending: true,
  //     });
  //     getCodeStudent({
  //       code: e.target.value,
  //       semesterId: values.semesterId,
  //       yearEduId: values.yearEduId,
  //     })
  //       .then(res => {
  //         // eslint-disable-next-line array-callback-return
  //         dataAddReward.map((item, index) => {
  //           if (index === number) {
  //             item.id = res.data.id;
  //             item.fullname = res.data.fullname;
  //             item.dob = res.data.dob;
  //             item.className = res.data.className;
  //             item.classId = res.data.classId;
  //             item.studyClassification = res.data.studyClassification;
  //             item.practiceClassification = res.data.practiceClassification;
  //             item.gradeTitle = res.data.gradeTitle;
  //           }
  //         });
  //         this.setState({
  //           dataAddReward,
  //           isPending: false,
  //         });
  //       })
  //       .catch(error => {
  //         console.log('error', error);
  //         this.setState({
  //           isPending: false,
  //         });
  //       });
  //   });
  // };

  changeBonusTypeChild = (value, record) => {
    const { dataAddReward } = this.state;
    const { randomCode } = record;
    dataAddReward[randomCode].bonusTypeId = value;
    this.setState({
      dataAddReward,
    });
  };

  chooseBonusType = (value, record = '') => {
    const { dataAddReward } = this.state;
    const { randomCode } = record;
    this.setState({
      isPending: true,
    });
    getBonusTypeChild(value)
      .then(res => {
        this.setState({
          bonusTypeChild: {
            [value]: res.data.items,
          },

          bonusTypeChildNew: res.data.items,
        });
        this.setState({
          isPending: false,
        });
        if (record) {
          // eslint-disable-next-line array-callback-return
          dataAddReward[randomCode].bonusTypeChildId = value;
          this.setState({
            dataAddReward,
          });
        }
      })
      .catch(error => {
        console.log(error);
        this.setState({
          isPending: false,
        });
      });
  };

  makeid = length => {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    for (let i = 0; i < length; i += +1) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  };

  render() {
    const {
      initialValue,
      dataAddReward,
      isPending,
      bonusType,
      teacherId,
      bonusTypeChild,
      bonusTypeChildNew,
      listOptionClass,
    } = this.state;
    const { getFieldDecorator } = this.props.form;
    const columns = [
      {
        title: 'Lớp',
        dataIndex: 'classId',
        key: 'classId',
        // eslint-disable-next-line no-unused-vars
        render: (text, record, index) => {
          console.log('record Mã sinh viên', record);
          return (
            <Select
              style={{ width: '100%' }}
              // defaultValue={record.parentBonusTypeId}
              onChange={(value, option) => this.changeOptionClass(record, value, option)}
            >
              {listOptionClass.map(item => (
                <Option key={item.classId} value={item.classId}>
                  {item.className}
                </Option>
              ))}
            </Select>
          );
        },
      },
      {
        title: 'Sỉ số',
        dataIndex: 'noOfStudent',
        key: 'noOfStudent',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'HS khá',
        dataIndex: 'noOfGreat',
        key: 'noOfGreat',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'HS giỏi',
        dataIndex: 'noOfGood',
        key: 'noOfGood',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'HS xuất sắc',
        dataIndex: 'noOfExcellent',
        key: 'noOfExcellent',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Danh hiệu',
        dataIndex: 'gradeTitle',
        key: 'gradeTitle',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Cấp khen thưởng',
        render: (text, record) => (
          <Select
            style={{ width: '100%' }}
            defaultValue={record.parentBonusTypeId}
            onChange={e => this.chooseBonusType(e, record)}
          >
            {bonusType.map(item => (
              <Option key={item.id} value={item.id}>
                {item.label}
              </Option>
            ))}
          </Select>
        ),
      },
      {
        title: 'Loại khen thưởng',
        render: (text, record) => (
          <Select
            style={{ width: '100%' }}
            value={record.bonusTypeId}
            onChange={e => this.changeBonusTypeChild(e, record)}
          >
            {record.bonusTypeId && record.approvalId
              ? (bonusTypeChild[record.parentBonusTypeId] || []).map(item => (
                  // eslint-disable-next-line react/jsx-indent
                  <Option key={item.id} value={item.id}>
                    {item.label}
                  </Option>
                ))
              : bonusTypeChildNew.map(item => (
                  // eslint-disable-next-line react/jsx-indent
                  <Option key={item.id} value={item.id}>
                    {item.label}
                  </Option>
                ))}
          </Select>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'rewardType',
        render: (text, record) => (
          <Button
            className="btn-delete"
            icon="delete"
            size="small"
            onClick={() => this.deleteData(record)}
          />
        ),
      },
    ];
    const optionFieldDecorator = {
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Card title={teacherId ? 'Cập nhật khen thưởng' : 'Thêm khen thưởng'}>
        <Row gutter={24}>
          <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
            <Col>
              <YearSemesterInput
                form={this.props.form}
                optionFieldDecorator={optionFieldDecorator}
              />
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Số QĐ" hasFeedback>
                {getFieldDecorator('approvalNumber', {
                  initialValue: initialValue.approvalNumber,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={6}>
              <Form.Item label="Ngày QD" hasFeedback>
                {getFieldDecorator('approvalDate', {
                  initialValue: initialValue.approvalDate,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<DatePickerString placeholder="Chọn ngày" />)}
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={8} style={{ marginLeft: 10 }}>
              <Form.Item label="Hình thức" hasFeedback>
                {getFieldDecorator('description', {
                  initialValue: initialValue.description,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
            </Col>

            <Col xs={24} sm={12} md={6}>
              <Form.Item label=" ">
                <BtnSave htmlType="submit" />
              </Form.Item>
            </Col>
          </Form>
        </Row>
        <Row>
          <BtnAddNew onClick={this.addDataAddReward} />
        </Row>
        <Row>
          <Col span={24}>
            <Spin spinning={isPending}>
              <Table
                onRow={this.rowTable}
                columns={columns}
                dataSource={Object.values(dataAddReward)}
                rowKey="randomCode"
              />
            </Spin>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



